import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Nav, Load, Footer } from "../components";

interface Prop {
    path: string;
    useDarkTheme: any;
    setUseDarkTheme: any;
}

const Container = styled.div`
    width: 100%;
    min-height: 100vh;
    background: ${(props) => props.theme.backgroundColor};
`;

const ImageContainer = styled.div`
    height: 80vh;
    background: red;
`

const ShopName = styled.h2`
    text-align: center;
    font-size: ${props => props.theme.header2}
    color: ${prop => prop.theme.textColor};
`

const Description = styled.div`
    color: ${prop => prop.theme.textColor};
`

export const Shop: React.FC<Prop> = ({ useDarkTheme, setUseDarkTheme }) => {
    const [ready, setReady] = useState<null | boolean>(null);

    useEffect(() => {
        // call the server
        // make a dummy call
        setTimeout(() => {
            setReady(true);
        }, 1000);
    });

    return (
        <>
            {ready === null ? (
                <Load />
            ) : (
                <>
                    {ready === true ? (
                        <Container>
                            <Nav useDarkTheme={useDarkTheme} setUseDarkTheme={setUseDarkTheme} />
                            <ImageContainer>Images</ImageContainer>
                            <ShopName>Shop Name</ShopName>
                            <Description>Shop Descriptions</Description>
                            <Footer />
                        </Container>
                    ) : (
                        <div>No Result Found !</div>
                    )}
                </>
            )}
        </>
    );
};
